


















































export default {
	data() {
		return {
			used_num:[],
			isAlert:false,
			sum:{
				'code_content':0,
				'use_code_total':0
			},
			isAble:false,
			isSuccess:false,
			total:'',
			page:1,
			isScroll:false
		};
	},
	methods:{
		// 页面复制或者立即领取事件
		copyBtn(){
			uni.request({
				url: this.$apis.getActivation,
				method:'GET',
				header: {
					Authorization: uni.getStorageSync('jwt'),
				},
				success: (res) => {
					let data = res.data.data;
					this.used_num = [{code_content:data.code_content},...this.used_num,];
					this.total++;
					this.sum.use_code_total = data.surplus_total;
					if(data.use_code_total == 0){
						this.copyBtn = null;
						this.isAble = true;
					}
					this.isAlert = true;
				}
			});
		},
		//弹窗复制按钮事件
		copyActi(obj){
			//判断传入的数据
			let data;
			if(typeof(obj) == 'string'){
				data = obj;
			}
			else{data = obj.code_content};
			// 复制到剪贴板
			uni.setClipboardData({
				data: data,
				success: function () {
					this.isSuccess = true;
					let timer = setTimeout(function(){
						this.isSuccess = false;
						}.bind(this),500)
				}.bind(this)
			});
			this.isAlert = false;
		},
		cancelAlert(){
			this.isAlert = false;
		},
		// 上拉加载更多
		upper(){
			this.page = this.page+1;
			this.isScroll = true;
			uni.request({
				url: this.$apis.uesdActivation, 
				method:'POST',
				data:{
					page:this.page,
				},
				header: {
					 Authorization: uni.getStorageSync('jwt'),
				},
				success: (res) => {
					let data = res.data.data;
					if(this.page>data.last_page){
						this.upper=null;
						uni.showToast({
							title: '没有更多数据了',
						  icon: 'none'
						})
					}
					this.total = data.total;
					this.used_num = [...this.used_num,...data.data];
				}
			});
		}
	},
	onLoad(){
		// 首页激活码数量
		let value = uni.getStorageSync('jwt');
		uni.request({
			url: this.$apis.activation, 
			method:'GET',
			header: {
				 Authorization:value,
			},
			success: (res) => {
				let data = res.data.data;
				this.sum = data;
				if(data.use_code_total == 0){
					this.copyBtn = null;
					this.isAble = true;
				}
			}
		});
	// 初始化已用激活码列表
		uni.request({
			url: this.$apis.uesdActivation, 
			method:'POST',
			data:{
				page:1,
			},
			header: {
				 Authorization:value,
			},
			success: (res) => {
				this.total = res.data.data.total;
				let data = res.data.data.data;
				this.used_num = data;
			}
		});
	}
}
